import React, { useState } from "react";
import { Box, Button, FormControl, IconButton, Input, Spinner, useDisclosure, useToast } from "@chakra-ui/react";
import {
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
  } from '@chakra-ui/react';
import { ViewIcon } from "@chakra-ui/icons";
import { ChatState } from "../../context/chatProvider";
import UserBadgeItem from "../UserAvatar/UserBadgeItem";
import UserListItem from "../UserAvatar/UserListItem";
import axios from "axios";

const UpdateGroupChatModal = ({ fetchAgain, setFetchAgain, fetchMessages}) => {

    const { user, selectedChat, setSelectedChat } = ChatState();
    const { isOpen, onOpen, onClose } = useDisclosure();
    const [groupChatName, setGroupChatName] = useState();
    const [search, setSearch] = useState("");
    const [searchResults, setSearchResults] = useState([]);
    const [loading, setLoading]= useState(false);
    const [renameloading, setRenameloading] = useState(false);
    const toast = useToast();

    const handleSearch = async (query) => {
        setSearch(query);
        if(!query) {
            return;
        }
    
        try {
            setLoading(true)
    
            const config = {
                headers: {
                  Authorization: `Bearer ${user.token}`,
                },
              };
    
              const { data } = await axios.get(`/api/user?search=${search}`, config);
              console.log(data);
              setLoading(false);
              setSearchResults(data);
        } catch (error) {
            toast({
                title: "Error occured",
                description:"Failed to load search results",
                status: "error",
                duration: 5000,
                isClosable: true,
                position: "bottom-left"
            });
        }
    }

    const handleRemove = async(user1)=>{
        if (selectedChat.groupAdmin._id !== user._id && user1._id !== user._id) {
            toast({
              title: "Only admins can remove someone!",
              status: "error",
              duration: 5000,
              isClosable: true,
              position: "bottom",
            });
            return;
          }
          try {
            setLoading(true)
            const config = {
                headers: {
                  Authorization: `Bearer ${user.token}`,
                },
              };

              const {data} = await axios.put(`/api/chat/groupremove`, {
               chatId: selectedChat._id,
                userId: user1._id
              }, config);
              
             user1._id === user._id ? setSelectedChat() : setSelectedChat(data);
              setFetchAgain(!fetchAgain);
              fetchMessages();
              setLoading(false);


          } catch (error) {
            toast({
                title: "Error Occured!",
                description:error.response.data.message,
                status: "error",
                duration: 5000,
                isClosable: true,
                position: "bottom"
            });
            setLoading(false);
          }

    }
    const handleRename = async () => {
        if(!groupChatName) return

        try {
            setRenameloading(true)

            const config = {
                headers: {
                  Authorization: `Bearer ${user.token}`,
                },
              };

              const {data} = await axios.put(`/api/chat/rename`, {
                chatId: selectedChat._id,
                chatName: groupChatName
              }, config);
              setSelectedChat(data);
              setFetchAgain(!fetchAgain);
              setRenameloading(false);
        } catch (error) {
            toast({
                title: "Error Occured!",
                description:error.response.data.message,
                status: "error",
                duration: 5000,
                isClosable: true,
                position: "bottom"
            });
            setRenameloading(false);
        }

        setGroupChatName("");
    }

    const handleAddUser=  async (user1) => {
        if (selectedChat.users.find((u) => u._id === user1._id)) {
            toast({
              title: "User Already in group!",
              status: "error",
              duration: 5000,
              isClosable: true,
              position: "bottom",
            });
            return;
          }
          if (selectedChat.groupAdmin._id !== user._id) {
            toast({
              title: "Only admins can add someone!",
              status: "error",
              duration: 5000,
              isClosable: true,
              position: "bottom",
            });
            return;
          }

          try {
            setLoading(true)
            const config = {
                headers: {
                  Authorization: `Bearer ${user.token}`,
                },
              };

              const {data} = await axios.put(`/api/chat/groupadd`, {
                chatId: selectedChat._id,
                userId: user1._id
              }, config);
              
              setSelectedChat(data);
              setFetchAgain(!fetchAgain);
              setLoading(false);


          } catch (error) {
            toast({
                title: "Error Occured!",
                description:error.response.data.message,
                status: "error",
                duration: 5000,
                isClosable: true,
                position: "bottom"
            });
            setLoading(false);
          }
    }

   

    return (
        <>
          <IconButton display={{ base:"flex"}} icon={<ViewIcon/>} onClick={onOpen} isCentered/>
    
          <Modal isOpen={isOpen} onClose={onClose}>
            <ModalOverlay />
            <ModalContent>
              <ModalHeader
              fontSize="35px"
              fontFamily= "Helvetica"
              display="flex"
              justifyContent="center"
              >{selectedChat.chatName}</ModalHeader>
              <ModalCloseButton />
              <ModalBody>
                <Box
                width="100%"
                display="flex"
                flexWrap="wrap"
                pb={3}>
                    {selectedChat.users.map((u) => (
                        <UserBadgeItem key={user._id} user={u} handleFunction={() => handleRemove(u)}/>
                    ))}
                </Box>
                <FormControl display="flex">
              <Input
                placeholder="Chat Name"
                mb={3}
                value={groupChatName}
                onChange={(e) => setGroupChatName(e.target.value)}
              />
              <Button
                variant="solid"
                colorScheme="teal"
                ml={1}
                isLoading={renameloading}
                onClick={handleRename}
              >
                Update
              </Button>
            </FormControl>
            <FormControl>
              <Input
                placeholder="Add User to group"
                mb={1}
                onChange={(e) => handleSearch(e.target.value)}
              />
            </FormControl>

            {loading ? (
              <Spinner size="lg" />
            ) : (
              searchResults?.map((user) => (
                <UserListItem
                  key={user._id}
                  user={user}
                  handleFunction={() => handleAddUser(user)}
                />
              ))
            )}
              </ModalBody>
    
              <ModalFooter>
                <Button colorScheme='red'  onClick={() => handleRemove(user) }>
                  Leave Group
                </Button>
               
              </ModalFooter>
            </ModalContent>
          </Modal>
        </>
      )

}

export default UpdateGroupChatModal;